export const StyledCaseClose = styled.button``;

export const StyledNavListItem = styled.li<{
  nestedListCollapsed: boolean;
}>(({ theme, nestedListCollapsed }) => {
  const { navOpen } = useContext(AppShellContext);
  const activeBackground = tryCatch(() =>
    rgba('#000000', theme.base.transparency['transparent-4'])
  );

  const { rtl } = useDirection();

  return css`
    display: block;
    position: relative;
    ${StyledIcon} {
      font-size: 1.25rem;
    }

    ${StyledCaseClose} {
      display: flex;
      position: absolute;
      left: ${navOpen ? 'auto' : '100%'};
      opacity: 0;
      &:hover,
      &:focus {
        ${navOpen &&
        css`
          opacity: 1;
        `}
      }
      ${navOpen &&
      css`
        inset-inline-end: 1rem;
      `}
      top: 50%;
      bottom: 50%;
      margin: auto 0;
      color: rgba(255, 255, 255, 0.7);
      transition-property: transform, opacity;
      transition-duration: ${theme.base.animation.speed};
      transition-timing-function: ${theme.base.animation.timing.ease};
    }

    > :first-child {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      padding: calc(1.25 * ${theme.base.spacing}) 0;
      white-space: nowrap;
      color: rgba(255, 255, 255, 0.7);
      cursor: pointer;
      font-weight: ${theme.base['font-weight']['semi-bold']};
      text-decoration: none;

      span {
        overflow: hidden;
        text-overflow: ellipsis;
        color: inherit;
        white-space: nowrap;
      }

      &:hover,
      &:focus {
        color: #ffffff;
        background: rgba(255, 255, 255, 0.05);
      }

      &:hover + ${StyledCaseClose}, &:focus + ${StyledCaseClose} {
        ${navOpen &&
        css`
          opacity: 1;
        `}
      }

      &:focus {
        outline: none;
        box-shadow: inset ${theme.base.shadow['focus-inverted']};
      }

      ${StyledAppShellNavIcon}:last-child {
        position: absolute;
        left: ${navOpen ? 'auto' : '100%'};
        ${navOpen &&
        css`
          inset-inline-end: 1rem;
        `}
        margin: 0;
        opacity: ${navOpen ? '1' : '0'};
        transition-property: transform, opacity;
        transition-duration: ${theme.base.animation.speed};
        transition-timing-function: ${theme.base.animation.timing.ease};

        ${!nestedListCollapsed &&
        css`
          transform: rotateZ(${rtl ? '-90deg' : '90deg'});
        `}
      }

      ${StyledAppShellNavIcon}:first-child {
        flex-shrink: 0;
        margin: 0 calc((${navWidth} - 1.125rem) / 2);
      }
    }

    &[aria-current='page'] > :first-child {
      color: ${theme.base.colors.blue.light};
      background-color: ${activeBackground};
    }
  `;
});

StyledNavListItem.defaultProps = defaultThemeProp;

