import { forwardRef, FunctionComponent, Ref, useRef, useState, ElementType } from 'react';

import Flex from '../Flex';
import Image from '../Image';
import SearchInput from '../SearchInput';
import { useBreakpoint, useFocusWithin } from '../../hooks';
import { ForwardProps } from '../../types';
import BareButton from '../Button/BareButton';

import { AppHeaderProps } from './AppShell.types';
import AppShellOperator from './Operator';
import {
  StyledAppHeader,
  StyledAppHeaderInfo,
  StyledAppHeaderSearchForm,
  StyledAppHeaderSpacer,
  StyledAppHeaderText,
  StyledAppHeaderOperator
} from './AppShell.styles';

const AppHeader: FunctionComponent<AppHeaderProps & ForwardProps> = forwardRef(
  (
    {
      appName,
      fullImageSrc,
      imageSrc,
      href,
      onClick,
      portalName,
      target,
      operator,
      searchInput,
      searchLabel
    }: AppHeaderProps,
    ref: Ref<Element>
  ) => {
    const isMediumOrAbove = useBreakpoint('md');
    const searchRef = useRef(null);
    const [searchFocused, setSearchFocused] = useState(false);

    useFocusWithin([searchRef], setSearchFocused);

    let appInfoAs: ElementType = 'div';
    if (href) appInfoAs = 'a';
    else if (onClick) appInfoAs = BareButton;

    return (
      <Flex
        as={StyledAppHeader}
        container={{ alignItems: 'center', pad: 1 }}
        hideTitle={!isMediumOrAbove && searchFocused}
        ref={ref}
      >
        <StyledAppHeaderInfo centerLogo={!fullImageSrc}>
          <Flex
            container={{ inline: true, alignItems: 'center', gap: 1 }}
            as={appInfoAs}
            href={href}
            onClick={onClick}
            target={target}
          >
            <Image src={fullImageSrc || imageSrc} alt='' aria-hidden='true' />
            {(isMediumOrAbove || !searchFocused) && (
              <>
                <StyledAppHeaderText>{appName}</StyledAppHeaderText>
                <StyledAppHeaderText variant='secondary'>{portalName}</StyledAppHeaderText>
              </>
            )}
          </Flex>
        </StyledAppHeaderInfo>

        {searchInput ? (
          <StyledAppHeaderSearchForm
            ref={searchRef}
            role='search'
            aria-label={searchLabel}
            onSubmit={e => e.preventDefault()}
            isMediumOrAbove={isMediumOrAbove}
          >
            <SearchInput {...searchInput} />
          </StyledAppHeaderSearchForm>
        ) : (
          <StyledAppHeaderSpacer />
        )}

        <AppShellOperator
          as={StyledAppHeaderOperator}
          actions={operator.actions}
          popover={{ placement: 'bottom-start' }}
        >
          {operator.avatar}
        </AppShellOperator>
      </Flex>
    );
  }
);

export default AppHeader;
