import { memo } from 'react';
import styled, { css } from 'styled-components';
import { hideVisually } from 'polished';

import { defaultThemeProp } from '../../theme';
import { documentIsAvailable } from '../../utils';
import { useI18n } from '../../hooks';
import Button from '../Button';
import Popover from '../Popover';

export const StyledSkipNavigation = styled(Popover)(props => {
  const {
    theme: {
      base: { 'z-index': zIndices, spacing }
    }
  } = props;

  const z = Math.max(...Object.values(zIndices)) + 1;

  return css`
    position: fixed;
    z-index: ${z};
    padding: ${spacing};

    &:not(:focus-within) {
      ${hideVisually()}
    }
  `;
});

StyledSkipNavigation.defaultProps = defaultThemeProp;

const onClick = () => {
  const main = documentIsAvailable ? document.querySelector('main') : null;
  if (!main) return;

  const heading = (['h1', 'h2', 'h3', 'h4', 'h5', 'h6'] as const).reduce<HTMLHeadingElement | null>(
    (el, tag) => el ?? main.querySelector(tag),
    null
  );

  if (heading?.textContent && main.getAttribute('aria-label') === null) {
    main.setAttribute('aria-label', heading.textContent);
    main.addEventListener(
      'focusout',
      () => {
        main.removeAttribute('aria-label');
      },
      { once: true }
    );
  }

  main.focus();
};

export default memo(() => {
  const t = useI18n();

  return (
    <StyledSkipNavigation
      show
      placement='bottom-end'
      target={{
        getBoundingClientRect: () => ({
          height: 0,
          width: 0,
          x: 24,
          y: 24,
          top: 24,
          bottom: 24,
          left: 24,
          right: 24
        })
      }}
    >
      <Button variant='link' onClick={onClick}>
        {t('go_to_main_content')}
      </Button>
    </StyledSkipNavigation>
  );
});
