import {
  FunctionComponent,
  PropsWithoutRef,
  Ref,
  forwardRef,
  useState,
  ChangeEvent,
  useCallback,
  KeyboardEvent,
  useEffect
} from 'react';
import styled, { css } from 'styled-components';
import { transparentize } from 'polished';

import { ForwardProps, NoChildrenProp } from '../../types';
import { defaultThemeProp } from '../../theme';
import Input from '../Input';
import Icon, { registerIcon } from '../Icon';
import * as searchIcon from '../Icon/icons/search.icon';
import { FormControlProps } from '../FormControl';
import { useAfterInitialEffect, useConsolidatedRef, useI18n } from '../../hooks';
import Flex from '../Flex';
import { tryCatch } from '../../utils';

registerIcon(searchIcon);

export interface SearchInputProps extends NoChildrenProp {
  /**
   * Placeholder text. The default value is a locale translation of 'Search'
   * @default "Search"
   */
  placeholder?: FormControlProps['placeholder'];
  /**
   * Creates a controlled input and sets the value. Requires an onChange handler to update value.
   * value + onChange is the recommended method per React team.
   */
  value?: FormControlProps['value'];
  /**
   * If you wish to use an [uncontrolled input](https://reactjs.org/docs/uncontrolled-components.html), pass a defaultValue prop and a ref to access the input's native value prop or by other DOM ref means.
   */
  defaultValue?: FormControlProps['defaultValue'];
  /** Called when the SearchInput value changes. */
  onSearchChange?: (value: string) => void;
  /**
   * Called when the SearchButton is clicked or when the Enter key is pressed while SearchInput is in focus.
   * NOTE: this does not trigger an actual form submit event. It simply represents an explicit action by the user to search as opposed to onSearchChange.
   */
  onSearchSubmit?: (value: string) => void;
  /** Aria label for search input, used by screen readers. */
  searchInputAriaLabel?: string;
}

export const StyledSearchIcon = styled.div(props => {
  const {
    theme: {
      base,
      components: { 'search-input': searchInput, 'form-control': formControl }
    }
  } = props;
  const iconColor = tryCatch(() => transparentize(0.75, base.palette['foreground-color']));
  return css`
    position: absolute;
    z-index: 1;
    inset-inline-start: 0.0625rem;
    top: 0.0625rem;
    width: calc(${base['hit-area']['mouse-min']} - 0.125rem);
    height: calc(${base['hit-area']['mouse-min']} - 0.125rem);
    border-radius: calc(${base['border-radius']} * ${searchInput['border-radius']});
    padding: 0;
    background: ${formControl['background-color']};
    color: ${iconColor};
    cursor: text;
    line-height: 1;
  `;
});

StyledSearchIcon.defaultProps = defaultThemeProp;

export const StyledSearchTextInput = styled(Input)(props => {
  const { theme } = props;
  const comp = theme.components['search-input'];
  return css`
    position: relative;
    width: 100%;
    height: ${theme.base['hit-area']['mouse-min']};
    min-height: ${theme.base['hit-area']['mouse-min']};
    padding-inline-start: 0;
    padding-inline-end: calc(${theme.base['hit-area']['mouse-min']} - 0.125rem);
    text-indent: calc(${theme.base['hit-area']['mouse-min']} - 0.125rem);
    border-radius: calc(${comp['border-radius']} * ${theme.base['border-radius']});
    &::-ms-clear {
      display: none;
    }
    &::-webkit-search-cancel-button {
      display: none;
    }
    ::-webkit-search-decoration {
      -webkit-appearance: none;
    }

    &:focus:not([disabled]) {
      box-shadow: none;
    }
  `;
});

StyledSearchTextInput.defaultProps = defaultThemeProp;

export const StyledSearchInput = styled.div(({ theme: { base, components } }) => {
  const iconColor = tryCatch(() => transparentize(0.3, base.palette['foreground-color']));

  return css`
    position: relative;
    height: ${base['hit-area']['mouse-min']};
    border-radius: calc(${components['search-input']['border-radius']} * ${base['border-radius']});

    &:focus-within {
      box-shadow: ${base.shadow.focus};

      ${StyledSearchIcon} {
        color: ${iconColor};
      }
    }
  `;
});

StyledSearchInput.defaultProps = defaultThemeProp;

const SearchInput: FunctionComponent<SearchInputProps & ForwardProps> = forwardRef(
  (props: PropsWithoutRef<SearchInputProps>, ref: Ref<HTMLInputElement>) => {
    const t = useI18n();

    const {
      defaultValue,
      value,
      placeholder = t('search_placeholder_default'),
      onSearchChange,
      onSearchSubmit,
      searchInputAriaLabel,
      ...restProps
    } = props;

    const [searchInputValue, setValue] = useState<string>(value || defaultValue || '');

    const innerRef = useConsolidatedRef(ref);

    useEffect(() => {
      if (typeof value === 'string' && value !== searchInputValue) {
        setValue(value);
      }
    }, [value]);

    const onChange = useCallback(
      (e: ChangeEvent<HTMLInputElement>) => {
        setValue(e.target.value);
      },
      [searchInputValue]
    );

    const onSubmit = useCallback(() => {
      onSearchSubmit?.(searchInputValue);
    }, [searchInputValue]);

    const onKeyDown = useCallback(
      (e: KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter') {
          onSubmit();
        }
      },
      [searchInputValue]
    );

    useAfterInitialEffect(() => {
      if (onSearchChange) onSearchChange(searchInputValue);
    }, [searchInputValue]);

    return (
      <Flex container as={StyledSearchInput}>
        <StyledSearchTextInput
          ref={innerRef}
          type='search'
          aria-label={searchInputAriaLabel}
          placeholder={placeholder}
          {...restProps}
          value={searchInputValue}
          onChange={onChange}
          onKeyDown={onKeyDown}
        />
        <Flex
          as={StyledSearchIcon}
          container={{ justify: 'center', alignItems: 'center' }}
          onClick={() => {
            innerRef.current?.focus();
          }}
        >
          <Icon name='search' />
        </Flex>
      </Flex>
    );
  }
);

export default SearchInput;
