import { FC, forwardRef, Ref } from 'react';
import styled, { DefaultTheme, css } from 'styled-components';

import { defaultThemeProp } from '@pega/cosmos-react-core';

export const getTableStyles = (theme: DefaultTheme) => css`
  border-collapse: collapse;

  td {
    border: 0.0625rem solid ${theme.base.palette['border-line']};
    min-width: 6.25rem;
  }

  tr:first-child {
    td:first-child {
      border-top-left-radius: 0.125rem;
    }

    td:last-child {
      border-top-right-radius: 0.125rem;
    }
  }

  tr:last-child {
    td:first-child {
      border-bottom-left-radius: 0.125rem;
    }

    td:last-child {
      border-bottom-right-radius: 0.125rem;
    }
  }
`;

const StyledTable = styled.table(props => getTableStyles(props.theme));

StyledTable.defaultProps = defaultThemeProp;

export const TableBody: FC<any> = forwardRef((props: any, ref: Ref<any>) => {
  const { children, ...restProps } = props;
  return (
    <tbody {...restProps} ref={ref}>
      {children}
    </tbody>
  );
});

export const Table: FC<any> = forwardRef((props: any, ref: Ref<any>) => {
  const { children, ...restProps } = props;
  return (
    <StyledTable {...restProps} ref={ref}>
      {children}
    </StyledTable>
  );
});

export const TableRow: FC<any> = forwardRef((props: any, ref: Ref<any>) => {
  const { children, ...restProps } = props;
  return (
    <tr {...restProps} ref={ref}>
      {children}
    </tr>
  );
});

export const TableCell: FC<any> = forwardRef((props: any, ref: Ref<any>) => {
  const { children, ...restProps } = props;
  return (
    <td {...restProps} ref={ref}>
      {children}
    </td>
  );
});
