import { forwardRef, FunctionComponent, PropsWithoutRef, Ref, useMemo } from 'react';

import { BaseProps, ForwardProps, NoChildrenProp } from '../../types';
import { useConfiguration } from '../../hooks';

import { getDisplayValue } from './utils';

export interface CurrencyDisplayProps extends BaseProps, NoChildrenProp {
  /** Number value */
  value?: string;
  /** Three letter currency shorthand ISO code. If not provided or invalid, component renders as number */
  currencyISOCode?: string;
  /** Always show ISO code
   * @default false
   * */
  alwaysShowISOCode?: boolean;
  formattingOptions?: {
    /** Determines whether decimal places should be shown. The value is simply truncated if true.
     * @default true
     */
    showDecimal?: boolean;
    /** Determines whether group separators should be shown.
     * @default true
     */
    showGroupSeparators?: boolean;
  };
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLAnchorElement>;
}

const CurrencyDisplay: FunctionComponent<CurrencyDisplayProps & ForwardProps> = forwardRef(
  (
    {
      value = '',
      currencyISOCode,
      alwaysShowISOCode = false,
      formattingOptions = {},
      ...restProps
    }: PropsWithoutRef<CurrencyDisplayProps>,
    ref: CurrencyDisplayProps['ref']
  ) => {
    const { locale } = useConfiguration();
    const { showDecimal = true, showGroupSeparators = true } = formattingOptions;

    const displayValue = useMemo(
      () =>
        value ? (
          getDisplayValue({
            value,
            locale,
            alwaysShowISOCode,
            showDecimal,
            showGroupSeparators,
            currencyISOCode
          })
        ) : (
          <span aria-hidden='true'>&ndash;&ndash;</span>
        ),
      [value, locale, alwaysShowISOCode, showDecimal, showGroupSeparators, currencyISOCode]
    );

    return (
      <span ref={ref} {...restProps}>
        {displayValue}
      </span>
    );
  }
);

export default CurrencyDisplay;
