import { FC, Ref, forwardRef, useMemo, useState, useEffect } from 'react';
import { Slate, Editable, withReact } from 'slate-react';
import { Descendant, createEditor } from 'slate';
import dompurify from 'dompurify';

import { ForwardProps, ErrorState, useI18n } from '@pega/cosmos-react-core';

import convertMarkdown from './utils/markdownConverter';
import { nodeRenderer, leafRenderer } from './utils/renderers';
import { RichTextViewerProps } from './RichTextViewer.types';
import { StyledEditorContainer, StyledHtml, StyledRichTextViewer } from './RichTextEditor.styles';
import { convertHtml } from './utils/htmlConverter';

// Regex to match valid URLs
const URL_REGEX =
  /(?<leadingChar>^|[^(:/a-z0-9@])(?<url>(?<protocol>https?:\/\/)?(?<host>[a-z0-9]+(?:[-.][a-z0-9]+)*\.[a-z]{2,5}(?<port>:[0-9]{1,5})?)(?<pathQueryHash>\/\S*)?)(?<trailingChar>$|[^):/a-z0-9])/g;

const RichTextViewer: FC<RichTextViewerProps & ForwardProps> = forwardRef(
  (
    {
      content,
      type,
      interactionRenderers,
      markdownMap,
      onRetry,
      ...restProps
    }: RichTextViewerProps,
    ref: Ref<HTMLDivElement>
  ) => {
    const t = useI18n();
    const editor = useMemo(() => withReact(createEditor()), []);
    // State is unused, but still set to force a re-render when updating editor.children.
    const [, setNodes] = useState<Descendant[]>([]);
    const [error, setError] = useState(false);

    useEffect(() => {
      if (type === 'markdown') {
        editor.children = convertMarkdown(
          content,
          [...(interactionRenderers || []), { regexPattern: URL_REGEX, type: 'builtin-url' }],
          markdownMap
        );
        setNodes(editor.children);
      }
      if (type === 'richtext') {
        editor.children = JSON.parse(content);
        setNodes(editor.children);
      }
      if (type === 'html' && interactionRenderers && interactionRenderers.length > 0) {
        try {
          editor.children = convertHtml(content, interactionRenderers);
          setNodes(editor.children);
          setError(false);
        } catch {
          editor.children = [];
          setNodes(editor.children);
          setError(true);
        }
      }
    }, [editor, content, type, interactionRenderers]);
    const sanitizedHtml = useMemo(() => {
      return type === 'html' && (!interactionRenderers || interactionRenderers.length === 0)
        ? dompurify.sanitize(content)
        : '';
    }, [content, type, interactionRenderers]);

    if (sanitizedHtml) {
      return <StyledHtml ref={ref} dangerouslySetInnerHTML={{ __html: sanitizedHtml }} />;
    }

    return error ? (
      <ErrorState message={t('rte_invalid_html')} onRetry={onRetry} />
    ) : (
      <StyledRichTextViewer ref={ref} {...restProps}>
        <Slate editor={editor} value={[]} onChange={() => {}}>
          <StyledEditorContainer>
            <Editable readOnly renderElement={nodeRenderer} renderLeaf={leafRenderer} />
          </StyledEditorContainer>
        </Slate>
      </StyledRichTextViewer>
    );
  }
);

export default RichTextViewer;
