import { useContext, useRef, ReactNode } from 'react';
import { lighten } from 'polished';

import BareButton from '../Button/BareButton';
import Drawer from '../Drawer';
import Flex from '../Flex';
import Icon, { registerIcon } from '../Icon';
import * as arrowLeftIcon from '../Icon/icons/arrow-left.icon';
import Text from '../Text';
import { useDirection, useI18n, useTheme } from '../../hooks';
import Configuration from '../Configuration';
import { tryCatch } from '../../utils';

import AppShellContext from './AppShellContext';
import {
  StyledDrawerWrapper,
  StyledDrawerBackButton,
  StyledDrawerHeading,
  StyledDrawerListContainer
} from './AppShell.styles';

registerIcon(arrowLeftIcon);

interface DrawerViewProps {
  content: ReactNode;
  header: string;
  onDrawerOpen?: () => void;
  onDrawerClose?: () => void;
}

export default (props: DrawerViewProps) => {
  const { setDrawerOpen, drawerOpen, refocusEl, focusedImperatively } = useContext(AppShellContext);
  const { content, header, onDrawerOpen, onDrawerClose } = props;
  const theme = useTheme();

  const backButtonRef = useRef<HTMLButtonElement>(null);
  const appShellBg = tryCatch(() =>
    lighten(0.1, theme.components['app-shell'].nav['background-color'])
  );
  const appShellTheme = {
    base: {
      palette: {
        'primary-background': appShellBg,
        'foreground-color': 'rgba(255, 255, 255, 0.7)',
        interactive: theme.base.colors.blue.light
      },
      shadow: {
        focus: theme.base.shadow['focus-inverted']
      }
    }
  };

  const { ltr, rtl } = useDirection();
  const t = useI18n();

  return (
    <Configuration theme={appShellTheme}>
      <Drawer
        as={StyledDrawerWrapper}
        position='absolute'
        placement='right'
        open={drawerOpen}
        nullWhenClosed
        onAfterOpen={() => {
          if (backButtonRef.current && document.activeElement !== backButtonRef.current) {
            focusedImperatively.current = true;
            backButtonRef.current.focus();
          }
          onDrawerOpen?.();
        }}
        onAfterClose={() => {
          if (refocusEl && document.activeElement !== refocusEl) {
            focusedImperatively.current = true;
            refocusEl.focus();
          }
          onDrawerClose?.();
        }}
      >
        <Flex as={StyledDrawerHeading} container={{ alignItems: 'center' }}>
          {rtl && <Text>{header}</Text>}
          <BareButton
            as={StyledDrawerBackButton}
            ref={backButtonRef}
            onClick={() => setDrawerOpen(false)}
            aria-label={t('close_noun', [header])}
          >
            <Icon name='arrow-left' />
          </BareButton>
          {ltr && <Text>{header}</Text>}
        </Flex>
        <Flex as={StyledDrawerListContainer} container={{ direction: 'column' }}>
          {content}
        </Flex>
      </Drawer>
    </Configuration>
  );
};
