import { forwardRef, FunctionComponent, PropsWithoutRef, Ref } from 'react';

import { BaseProps, ForwardProps } from '../../types';
import { useConfiguration, useI18n } from '../../hooks';

import {
  ClockFormat,
  getEndOfWeek,
  getQuarter,
  getStartOfWeek,
  getWeek,
  getWeekYear,
  parseToDate
} from './Input/utils';
import { isValidDate } from './Picker/utils';

type AbsoluteVariant =
  | 'date'
  | 'datetime'
  | 'time'
  | 'week'
  | 'month'
  | 'year'
  | 'monthyear'
  | 'quarteryear';
export type DateTimeVariant = AbsoluteVariant | 'relative';
export type DateTimeFormat = 'short' | 'long' | 'numeric';

export interface DateTimeDisplayProps extends BaseProps {
  /**
   * Date as ISO8601 string, timestamp or native Date object.
   */
  value?: Date | string | number;
  /**
   * Controls date time variant.
   */
  variant: DateTimeVariant;
  /**
   * Controls display format.
   * @default "short"
   */
  format?: DateTimeFormat;
  /**
   * Defines clock format that overrides locale based format
   */
  clockFormat?: ClockFormat;
  /** Ref for the wrapping element. */
  ref?: Ref<HTMLSpanElement>;
}

const unitsInMs = {
  year: 24 * 60 * 60 * 1000 * 365,
  month: (24 * 60 * 60 * 1000 * 365) / 12,
  day: 24 * 60 * 60 * 1000,
  hour: 60 * 60 * 1000,
  minute: 60 * 1000,
  second: 1000
} as const;

type TimeUnit = keyof typeof unitsInMs;

const DateTimeDisplay: FunctionComponent<DateTimeDisplayProps & ForwardProps> = forwardRef(
  (
    {
      value,
      variant,
      format = 'short',
      clockFormat,
      ...restProps
    }: PropsWithoutRef<DateTimeDisplayProps>,
    ref: DateTimeDisplayProps['ref']
  ) => {
    const { locale } = useConfiguration();
    const t = useI18n();

    const timeShort = {
      hour: 'numeric',
      minute: 'numeric',
      hour12: clockFormat ? clockFormat === 12 : undefined
    } as const;
    const timeLong = {
      ...timeShort,
      second: 'numeric',
      hour12: clockFormat ? clockFormat === 12 : undefined
    } as const;

    const formatMapping = {
      date: {
        short: { year: 'numeric', month: 'short', day: 'numeric' } as const,
        long: { year: 'numeric', month: 'long', day: 'numeric' } as const,
        numeric: { year: 'numeric', month: 'numeric', day: 'numeric' } as const
      },
      datetime: {
        short: { year: 'numeric', month: 'short', day: 'numeric', ...timeShort } as const,
        long: { year: 'numeric', month: 'long', day: 'numeric', ...timeLong } as const,
        numeric: { year: 'numeric', month: 'numeric', day: 'numeric', ...timeShort } as const
      },
      time: {
        short: timeShort,
        long: timeLong,
        numeric: timeShort
      },
      month: {
        short: { month: 'short' } as const,
        long: { month: 'long' } as const,
        numeric: { month: 'numeric' } as const
      },
      year: {
        short: { year: 'numeric' } as const,
        long: { year: 'numeric' } as const,
        numeric: { year: '2-digit' } as const
      },
      monthyear: {
        short: { year: 'numeric', month: 'short' } as const,
        long: { year: 'numeric', month: 'long' } as const,
        numeric: { year: 'numeric', month: 'numeric' } as const
      },
      week: {
        short: { month: 'short', day: 'numeric' } as const,
        long: { month: 'long', day: 'numeric' } as const,
        numeric: { month: 'numeric', day: 'numeric' } as const
      },
      relative: {
        short: { style: 'long', numeric: 'auto' } as const,
        long: { style: 'long', numeric: 'auto' } as const,
        numeric: { style: 'short', numeric: 'auto' } as const
      }
    };

    const getFormattedDate = (date: Date, dateVariant: Exclude<AbsoluteVariant, 'quarteryear'>) => {
      return new Intl.DateTimeFormat(locale, {
        ...formatMapping[dateVariant][format],
        timeZone: 'UTC'
      }).format(date);
    };

    const relativeTimeFormatter = (relativeValue: number, unit: TimeUnit) => {
      return new Intl.RelativeTimeFormat(locale, formatMapping.relative[format]).format(
        relativeValue,
        unit
      );
    };

    const getRelativeTime = (date: Date) => {
      let elapsed = date.getTime() - Date.now();
      // Deliberately reduce precision to 1 minute
      if (Math.abs(elapsed) < 60000) elapsed = 0;
      const unit = (Object.keys(unitsInMs).find(
        u => Math.abs(elapsed) >= unitsInMs[u as TimeUnit]
      ) || 'second') as TimeUnit;
      return relativeTimeFormatter(Math.round(elapsed / unitsInMs[unit]), unit);
    };

    const getFormattedWeek = (date: Date) => {
      const startDate = getFormattedDate(getStartOfWeek(date), 'week');
      const endDate = getFormattedDate(getEndOfWeek(date), 'week');
      return `${t('week_text')} ${getWeek(date)}, ${getWeekYear(date)} (${startDate} - ${endDate})`;
    };

    const getFormattedQuarterYear = (date: Date) => {
      const formattedYear = getFormattedDate(date, 'year');
      const quarterNumber = getQuarter(date);
      return `${t(`date_quarter_q${quarterNumber}`, [formattedYear])}`;
    };

    const displayValue = (date: Date) => {
      switch (variant) {
        case 'relative':
          return getRelativeTime(date);
        case 'week':
          return getFormattedWeek(date);
        case 'quarteryear':
          return getFormattedQuarterYear(date);
        default:
          return getFormattedDate(date, variant);
      }
    };

    return (
      <span ref={ref} {...restProps}>
        {(value || value === 0) && isValidDate(parseToDate(value)) ? (
          displayValue(parseToDate(value))
        ) : (
          <span aria-hidden='true'>&ndash;&ndash;</span>
        )}
      </span>
    );
  }
);

export default DateTimeDisplay;
