import { forwardRef, useState, useEffect, useContext, useMemo } from 'react';
import type { Ref, ReactNode, MouseEvent, UIEvent } from 'react';

import BareButton from '../Button/BareButton';
import Popover from '../Popover';
import type { PopoverProps } from '../Popover';
import Menu from '../Menu';
import type { AcceptedMouseEventElement } from '../Menu/Menu.types';
import { useConsolidatedRef } from '../../hooks';
import type { AsProp, OmitStrict } from '../../types';

import AppShellContext from './AppShellContext';
import { OperatorProps } from './AppShell.types';

const Operator = forwardRef(
  (
    {
      children,
      actions,
      popover,
      as
    }: {
      children: ReactNode;
      actions: OperatorProps['actions'];
      popover?: OmitStrict<PopoverProps, 'show' | 'target' | 'children'>;
    } & AsProp,
    ref: Ref<HTMLButtonElement> | undefined
  ) => {
    const { navOpen } = useContext(AppShellContext);
    const buttonRef = useConsolidatedRef(ref);

    const operatorMenuItems = useMemo(() => {
      return actions.flat().map(({ id, text, onClick: click, ...rest }) => ({
        id,
        primary: text,
        onClick: click,
        ...rest
      }));
    }, [actions]);

    const [showMenu, setShowMenu] = useState(false);

    useEffect(() => {
      if (!navOpen) setShowMenu(false);
    }, [navOpen]);

    return (
      <>
        <BareButton
          as={as}
          ref={buttonRef}
          onClick={() => {
            setShowMenu(cur => !cur);
          }}
          onKeyDown={(e: KeyboardEvent) => {
            if (e.key === 'Escape') setShowMenu(false);
          }}
          onBlur={() => {
            setShowMenu(false);
          }}
        >
          {children}
        </BareButton>
        <Popover {...popover} show={showMenu} target={buttonRef.current}>
          <Menu
            focusControlEl={buttonRef.current ?? undefined}
            mode='action'
            items={operatorMenuItems}
            /**
             * @see {@link MenuButton} implementation for explanation of conditions. This is in essence duplicated.
             */
            onItemClick={(_, e) => {
              if (
                (e as MouseEvent<AcceptedMouseEventElement> & UIEvent<AcceptedMouseEventElement>)
                  .detail > 0
              ) {
                setShowMenu(false);
              }
            }}
          />
        </Popover>
      </>
    );
  }
);

export default Operator;
